@use '../../styles/vars';

.prismal-tabs-container {
    position: relative;
    .prismal-tabs {
        display: flex;
        flex-direction: row;
        width: 100%;
        background-color: vars.$light-color;
        gap: 1rem;
        justify-content: space-evenly;
        overflow-x: auto;
        overflow-y: clip;
        border-radius: var(--border-radius);
        font-family: vars.$font-body;
        /*
        &::after {
            content: "\a0";
            width: 100%;
            height: 1px;
            position: absolute;
            bottom: 0;
            background-color: grey;
        } */

        .prismal-tab-container {
            flex: 1;
            position: relative;

            .prismal-tab {
                display: flex;
                flex-direction: column;
                justify-content: center;
                min-width: 6rem;
                height: 2.5rem;
                align-items: center;
                border-radius: var(--border-radius);
                color: vars.$dark-color;
                cursor: pointer;
                
                &.selected {
                    color: vars.$primary-color;
                    font-weight: bold;
                }

                &.selected::after {
                    content: "\a0";
                    width: 4rem;
                    height: 3px;
                    background-color: vars.$primary-color;
                    position: absolute;
                    bottom: 0;
                }
            }
        }
    }

    .prismal-tab-content {
        padding: 1rem;
    }

}